import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { useLocation } from "react-router-dom";

import { connect } from "react-redux";
import HeroloTabs from "../components/HeroloTabs/HeroloTabs";
import Weather from "../Views/Weather/Weather";
import { Container } from "@material-ui/core";
import { useStyles } from "./style";
import Favorites from "../Views/Favorites/Favorites";
import appConfigActions from "./actions";
import geolocation from "geolocation";
import weatherActions from "../Views/Weather/actions";
import autocompleteActions from "../components/HeroloAutocomplete/actions";
const useQuery = () => new URLSearchParams(useLocation().search);

export const AppRouter = (props) => {
  const classes = useStyles();
  const query = useQuery();
  const URL_ROUTE_VALID = (props.match.params.locationId && query.get("search"))

  const dataInit = async () => {
    const geolocationPermission = await navigator.permissions.query({
      name: "geolocation",
    });

    // defualt location to TLV if geoloaction isnt enabled
    let longitude = "32.0853";
    let latitude = "34.7818";

    if (geolocationPermission.state === "granted") {
      geolocation.getCurrentPosition(async (err, position) => {
        if (err) throw err;
        latitude = position.coords.latitude;
        longitude = position.coords.longitude;
      });
    }
    debugger;
    if (!URL_ROUTE_VALID) {
      debugger;
      const data = await props.SET_WHEATHER_DATA_BY_GEOLOCATION( longitude, latitude );
      props.history.push( `/weather/${data.locationData.Key}/?search=${data.locationData.LocalizedName}` );
    }

    props.updateAppConfig({ isAppReady: true });
  };

  //   React.useEffect(() => {
  //       debugger;
  //     dataInit();
  //   }, []);

  React.useEffect(() => {
    debugger;
    if (!URL_ROUTE_VALID) dataInit();
    else props.updateAppConfig({ isAppReady: true });
  }, [URL_ROUTE_VALID]);

  return (
    <Container className={classes.root} maxWidth={false}>
      <Route
        path={"/:tab/:locationId?"}
        render={(url) => <HeroloTabs url={url} />}
      />
      <Switch>
        <Route path={"/weather/:locationId?"} component={Weather} />
        <Route path={"/favorites"} component={Favorites} />
        <Redirect exact to={"/weather"} />
      </Switch>
    </Container>
  );
};

// AppRouter.propTypes = {
//   prop: PropTypes,
// };

const mapStateToProps = (state) => {
  return {
    appConfig: state.appConfig,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    SET_WHEATHER_DATA_BY_GEOLOCATION: (lat, lon) =>
      dispatch(weatherActions.SET_WHEATHER_DATA_BY_GEOLOCATION(lat, lon)),
    // SET_WHEATHER_DATA_BY_KEY: (key) => dispatch(weatherActions.SET_WHEATHER_DATA_BY_KEY(key)),
    // SET_AUTOCOMPLETE_DATA_BY_QUERY: (query) => dispatch(autocompleteActions.SET_AUTOCOMPLETE_DATA_BY_QUERY(query)),
    updateAppConfig: (payload) =>
      dispatch(appConfigActions.UPDATE_APP_CONFIG(payload)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AppRouter);
